import React, { useEffect, useRef } from 'react';
import FlexView from 'react-flexview/lib';
import { TweenMax } from 'gsap';

import mcMescalineTea from '../../assets/images/Menu/Drugs/mcMescalineTea.png';
import mcToad from '../../assets/images/Menu/Drugs/mcToad.png';
import mcLSD from '../../assets/images/Menu/Drugs/mcLSD.png';
import doCoke from '../../assets/images/Menu/Drugs/doCoke.png';

import * as d from './DrugDescriptions';

export default function Drugs({ setClickedImg }) {
	let drugPics = useRef();

	useEffect(() => {
		TweenMax.from(drugPics, 2.2, {
			opacity: 0,
			x: -970,
			ease: 'elastic',
			delay: 0.3,
		});
	}, [drugPics]);

	return (
		<FlexView column className="menuScroller" hAlignContent="center">
			<div className="menuTitle">Our Drugs</div>
			<FlexView
				margin="auto"
				className="menuScrollFlex"
				ref={(el) => (drugPics = el)}
			>
				<div className="menuItem" onClick={() => setClickedImg(d.mescalineTea)}>
					<img src={mcMescalineTea} alt="Mescaline Tea" />
				</div>
				<div className="menuItem" onClick={() => setClickedImg(d.dmt)}>
					<img src={mcToad} alt="DMT" />
				</div>
				<div className="menuItem" onClick={() => setClickedImg(d.layingLSD)}>
					<img src={mcLSD} alt="LSD" />
				</div>
				<div
					className="menuItem"
					onClick={() => setClickedImg(d.acetoneWashCoke)}
				>
					<img src={doCoke} alt="Do Coke" />
				</div>
			</FlexView>
		</FlexView>
	);
}
